





































































































































































@import "@/styles/util/util.scss";

.wrapper {
    background: $color-white;
}
.job-block {
    @include rem((padding-top: 60px));
}

.job-detail {
    .block {
        h3 {
            @include rem((margin-bottom: 20px));
            color: $color-red-t2w;
        }
        ul {
            li {
                list-style-type: square;
            }
        }
        &.title {
            h2 {
                @include rem((padding: 20px 0));
                font-weight: bold;
                text-align: center;
            }
        }
        &.intro {
            p {
                @include rem((font-size, 16px));
            }
        }
        &.subtext {
            text-align: center;
        }
    }
}

.more-info {
    .box {
        @include rem((padding: 20px));
        background: $color-gray-light;
        h4 {
            @include rem((margin-bottom: 20px));
            font-weight: bold;
        }
        ul {
            @include rem((padding: 0));
            li {
                list-style-type: none;
                font-weight: bold;
                span {
                    font-weight: normal;
                }
                &.overpayment {
                    @include rem(font-size, 14px);
                    @include rem((margin-top: 10px));
                    font-weight: normal;
                    font-style: italic;
                }
                &.jobtitle {
                    color: $color-red-t2w;
                    span {
                        color: $color-black-medium;
                    }
                }
            }
        }
    }
    a.edit-job {
        @include rem((margin-top: 20px));
    }
    .apply-btn {
        @include rem((margin-top: 25px));
        a.btn-more-jobs {
            @extend %ghost-button-red;
        }
        a.btn-edit-job {
            @extend %ghost-button-green;
        }
    }
    .gdpr {
        @include rem((margin-top: 20px));
        @include rem(font-size, 18px);
        a {
            @include rem(font-size, 18px);
            color: $color-red-t2w;
            font-family: $font-regular, "Helvetica Neue", "Helvetica", "Arial", "sans-serif";
            &:disabled {
                border: 2px solid $color-black-medium;
            }
        }
    }
}

.salary-block {
    @include rem((padding-left: 0));
    @include rem((margin-bottom: 30px));
    h3 {
        @include rem((margin-bottom: 20px));
        color: $color-red-t2w;
    }
    .salary-box {
        @include rem((margin-left: 0px));
        @include rem((padding: 1px));
        border: 1px solid $color-blue-medium;
        .max,
        .min {
            @include rem((padding: 10px));
        }
        .min {
            background: $color-gray-light;
            text-align: center;
        }
        .max {
            background: rgba($color-red-t2w, 0.5);
            background: -moz-linear-gradient(left, rgba($color-red-t2w, 0.5) 0%, #ffffff 100%);
            background: linear-gradient(to right, rgba($color-red-t2w, 0.5) 0%,#ffffff 100%);

            span {
                &.current {
                    @include rem((margin-left: 20px));
                    text-align: left;
                    font-weight: bold;
                }
                &.maximal {
                    @include rem((margin-right: 20px));
                    float: right;
                }
            }
        }
    }
    .salary-desc {
        @include rem((margin-top: 20px));
        .min {
            text-align: center;
            span.min {
                @include rem((padding: 3px 5px 5px 5px));
                text-align: center;
                background: $color-gray-dark;
                &:before {
                    @include rem((margin-left: -10px));
                    content: '';
                    display: block;
                    position: absolute;
                    left: 50%;
                    top: -10px;
                    width: 0;
                    height: 0;
                    border-width: 0 15px 15px 15px;
                    border-style: solid;
                    border-color: transparent transparent $color-gray-dark transparent;
                }
            }
        }
        .actual {
            span.actual {
                @include rem((padding: 5px));
                background: $color-gray-dark;
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    left: 61px;
                    top: -4px;
                    width: 0;
                    height: 0;
                    border-width: 14px 0 14px 10px;
                    border-style: solid;
                    border-color: transparent transparent transparent $color-gray-dark;
                }
            }
        }
    }
    @media (max-width: $screen-sm) {
        display: none;
    }
}
